/* eslint-disable */
import react from 'react'
import { Tabbar, TabbarItem } from '@antmjs/vantui'

export default function Demo() {
  const [active, setActive] = react.useState<string | number>(0)
  return (
    <Tabbar
      activeColor="#07c160"
      inactiveColor="#000"
      active={active}
      onChange={(e) => setActive(e.detail)}
    >
      <TabbarItem icon="home-o"></TabbarItem>
      <TabbarItem icon="search"></TabbarItem>
      <TabbarItem icon="friends-o"></TabbarItem>
      <TabbarItem icon="setting-o"></TabbarItem>
    </Tabbar>
  )
}
